import React from "react";
import noAvaPhoto from '../../assets/images/no_ava.png';
import styles from "./User.module.css";
import {NavLink} from "react-router-dom";


let User = ({user, followingInProgress, follow, unFollow}) => {
    return (
        <div>
            <NavLink to={'/profile/' + user.id}>
                <div>
                    <img src={user.photos.small != null ? user.photos.small : noAvaPhoto} alt=""
                         className={styles.userPhoto}/>
                </div>
            </NavLink>
            <span className={styles.name}>{user.name}</span>
            <div className={styles.unic}>{user.status}</div>
            <div>
                {user.followed
                    //  id       followingInProgress.  followingInProgress      id  ,     , ..   ,    .     disabled = false,         followingInProgress( c id    ).     -   toggleFollowingProgress,     isFetching = true   followingInProgress  id .  false -    map    id ,       followingInProgress  id.      ...
                    ? <button disabled={followingInProgress.some(id => id === user.id)} onClick={() => {
                        unFollow(user.id);
                    }}></button>
                    /*some()            ,    .       true,       true (     ),       false (      ).*/
                    : <button disabled={followingInProgress.some(id => id === user.id)}
                              onClick={() => {
                                  follow(user.id);
                              }}></button>
                }
            </div>
        </div>
    )
}

export default User;